.layout {
    width: 1366px;
    height: 768px;
    justify-content: center;
    margin-left: 100px;
    margin-top: 50px;
    display: grid;
    grid:
      "sidebar body" 1fr
      / auto 1fr;
    gap: 8px;
  }
  
  .sidebar { grid-area: sidebar; }
  .body { grid-area: body; margin-left: 50px;}